/* CostCalculator.css */

.cost-calculator {
    max-width: 500px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f0f0f0;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.input-section {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-bottom: 20px;
}

.cost-input {
    display: flex;
    align-items: center;
}

label {
    flex: 1;
    font-weight: bold;
}

input {
    flex: 2;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

button {
    display: block;
    width: 100%;
    padding: 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

button:hover {
    background-color: #0056b3;
}

.output-section {
    margin-top: 20px;
}

h1 {
    text-align: center;
}

h2 {
    text-align: center;
    margin-top: 10px;
}

@media screen and (max-width: 500px) {
    .cost-calculator {
        padding: 10px;
    }

    input {
        font-size: 14px;
    }
}

/* CostCalculator.css */

.comment-input {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
}

.comment-input label {
    font-weight: bold;
}

.comment-input textarea {
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 5px;
    resize: vertical;
    /* Allow vertical resizing */
}

.comment-input textarea::placeholder {
    color: #aaa;
}

.comment-input textarea:focus {
    outline: none;
    border-color: #007bff;
    /* Change border color when focused */
}